import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";

const NavHeader = () => {
    return (
        <div className="nav-header position-fixed d-flex justify-content-center">
            <Row className="">
                <Col>
                    <Link to={"/"} className="nav-button">
                        HOME
                    </Link>
                </Col>
                <Col>
                    <Link to={"/info-and-faq"} className="nav-button">
                        INFO AND FAQ
                    </Link>
                </Col>
                <Col>
                    <Link to={"/terms-of-service"} className="nav-button">
                        T.O.S
                    </Link>
                </Col>
                <Col>
                    <Link to={"/order-tracking"} className="nav-button">
                        ORDER TRACKING
                    </Link>
                </Col>
            </Row>
        </div>
    );
}

export default NavHeader;